.password-widget__wrap{flex:0 0 calc(40px * 16 + 20px * 15)}.password-widget__generator-wrap{display:flex}.password-widget__generator-button-wrap{display:flex;align-items:center;margin-bottom:20px}.password-widget__generator-button{font-size:16px;color:#2686ED;font-weight:600}.password-widget__generator-input-wrap{position:relative;display:flex;margin-bottom:20px;flex:0 0 calc(40px * 10 + 20px * 9);margin-right:20px}.password-widget__auxiliary-input-wrap{position:relative;display:flex;flex:0 0 calc(40px * 6 + 20px * 5)}.password-widget__auxiliary-input{width:calc(40px * 6 + 20px * 5)}.generator-input__icon{position:absolute;right:15px;top:9px;color:#C8CACB;font-size:18px;transition:color .2s}.generator-input__icon:before{cursor:pointer}.generator-input__icon:hover{color:#23292e}.generator-input__icon_active{color:#23292e}.generator-input__icon_disabled{color:#C8CACB !important;pointer-events:none;cursor:default}
