.base-popup__wrap{position:fixed;top:0;left:0;display:flex;flex-direction:column;align-items:center;width:100vw;height:100vh;padding:40px 0;background-color:transparent;will-change:background-color;transition:background-color linear 0.2s;overflow:auto;z-index:1000;background:rgba(0,0,0,0.3)}.base-popup{margin:auto;background-color:#fff;border-radius:10px;position:relative;max-height:calc(100% - 40px);height:auto;display:flex;flex-direction:column}.base-popup__body{padding:20px 40px;display:flex;flex-direction:column}.base-popup__title-wrap{margin-bottom:30px}.base-popup__title{font-size:22px;color:#23292e;font-weight:600}.base-popup__icon-close{position:absolute;top:10px;right:15px;color:#A6A9AA;transition:color linear .2s;cursor:pointer;z-index:20}.base-popup__icon-close:before{font-size:13px}.base-popup__icon-close:hover{color:#23292e}.base-popup__footer-wrap{padding:0 60px;height:55px;border-top:1px solid #E7EAEE;display:flex;align-items:center;justify-content:flex-end}.base-popup__buttons-wrap{display:flex;justify-content:flex-end}.fade-enter-active{transition:all .2s}.fade-leave-active{transition:all .2s}.fade-enter,.fade-leave-to{opacity:0}
