.cp-radio-tabs__wrap{display:flex}.cp-radio-tabs__item{height:35px;padding:0 20px;white-space:nowrap;border-top:1px solid #D0D8DC;border-bottom:1px solid #D0D8DC;border-right:1px solid #D0D8DC;display:flex;justify-content:center;align-items:center;font-size:14px;cursor:pointer;background-color:#F8F8F8;transition:color 0.2s linear,background-color 0.2s linear}.cp-radio-tabs__item:first-child{border-top-left-radius:5px;border-bottom-left-radius:5px;border-left:1px solid #D0D8DC}.cp-radio-tabs__item:last-child{border-top-right-radius:5px;border-bottom-right-radius:5px}.cp-radio-tabs__item:hover{color:#2686ED}.cp-radio-tabs__item_selected{background-color:#E4F0FA}.cp-radio-tabs__input{z-index:-1;opacity:0;position:absolute;cursor:default}.cp-radio-tabs__label{cursor:pointer;position:relative;bottom:1px}
