.language-switcher__wrap{display:flex}.language-switcher__item{background-color:white;border-top:1px solid #D0D8DC;border-bottom:1px solid #D0D8DC;color:#23292e;font-size:14px;font-weight:600;width:36px;height:28px;transition:color 0.2s linear,background-color 0.2s linear,border-color 0.2s linear}.language-switcher__item:first-child{border-left:1px solid #D0D8DC;border-top-left-radius:17px;border-bottom-left-radius:17px}.language-switcher__item:first-child .language-switcher__label{left:2px}.language-switcher__item:last-child{border-top-right-radius:17px;border-bottom-right-radius:17px;border-right:1px solid #D0D8DC}.language-switcher__item:last-child .language-switcher__label{right:2px}.language-switcher__item:hover{color:#2686ED}.language-switcher__item_active{background-color:#EDF5FA;color:#23292e}.language-switcher__item_active:hover{color:#23292e}.language-switcher__item_active .language-switcher__label{cursor:default}.language-switcher__item_active.language-switcher__item_invalid:hover{color:#FF3455}.language-switcher__item_invalid{color:#FF3455}.language-switcher__item_invalid:hover{color:#A62137}.language-switcher__item_error-borders{border-top:1px solid #FF9FAF;border-bottom:1px solid #FF9FAF}.language-switcher__item_error-borders:first-child{border-left:1px solid #FF9FAF}.language-switcher__item_error-borders:last-child{border-right:1px solid #FF9FAF}.language-switcher__input{opacity:0;z-index:-1;position:absolute}.language-switcher__label{position:relative;width:100%;height:100%;cursor:pointer;display:flex;justify-content:center;align-items:center}
