.form-footer__wrap{width:100%;border-top:1px solid #EDF5FA;padding:0 40px;height:56px}.form-footer__box{font-size:14px;display:flex;justify-content:space-between;flex-shrink:0;margin-left:calc(20px + 40px * 4 + 20px * 3);margin-top:10px;margin-bottom:10px}.form-footer__item{flex:1;display:flex;flex-direction:column;justify-content:space-between}.form-footer__item:not(:last-child){border-right:1px solid #EDF5FA}.form-footer__item:not(:first-child){margin-left:15px}.form-footer__label-wrap{display:flex}.form-footer__label{font-weight:300}
