.button_clear{padding:0;border:none;font:inherit;color:inherit;background-color:transparent;cursor:pointer;font-size:14px;line-height:2px;transition:color .2s, background .2s}.button_clear:focus{outline:0}.button_radius-x{border-radius:18px}.button_radius-s{border-radius:5px}.button_red{font-size:16px;color:#FF3455;padding:10px 25px;font-weight:600}.button_red:hover{color:#A62137}.button_green{padding:10px 25px;background:#23CA62;color:white;font-weight:600}.button_green:hover{background:#1AB052}.button_height-x{height:36px}
