.label-component{line-height:18px;font-size:14px;font-weight:300}.label-component_left{height:36px;display:flex;justify-content:flex-end;align-items:center;flex-shrink:0;width:calc(40px * 4 + 20px * 3);margin-right:20px}.label-component_left .label-component{text-align:right}.label-component_left .label-component.label-component_required:before{content:'*';position:relative;left:0;margin-right:2px}.label-component_top{margin-left:15px;margin-bottom:10px;display:flex;align-items:center;justify-content:flex-end;flex-direction:row-reverse}.label-component_top .label-component.label-component_required:after{content:'*';position:relative;right:0;margin-left:2px}
