.cp-select{font-size:14px;position:relative}.cp-select__dropdown-list,.cp-select__dropdown-tags{position:absolute;top:0}.cp-select__input{font-size:14px;width:100%;color:#23292e;border-top-left-radius:5px;border-top-right-radius:5px;border-left:none;border-right:none;border-top:none;border-bottom:1px solid #EDF5FA;height:35px;padding:0 15px}.cp-select__input:focus{box-shadow:none}.cp-select__arrows{width:15px;height:15px;position:absolute;right:15px;top:11px;display:flex;justify-content:center;align-items:center;cursor:pointer}.cp-select__arrows:before{font-family:"cp-fonts";font-size:12px}.cp-select__arrow-down:before{content:"\e916"}.cp-select__arrow-down_blocked{cursor:default}.cp-select__arrow-up:before{content:"\e919"}.cp-select__tags{border:1px solid transparent;position:relative;top:-1px}.cp-select__hidden-input{position:absolute;width:0;height:0;opacity:0;z-index:-1}.dropdown-list{color:#23292e;list-style-type:none;padding:0 10px;margin:0;max-height:160px;overflow:auto}.dropdown-list__wrap{border-radius:5px;border:1px solid #D0D8DC;background:white;width:100%;z-index:10;box-shadow:0px 2px 10px rgba(0,0,0,0.2)}.dropdown-list__item{height:35px;padding:8px 5px 0 5px;cursor:pointer;transition:color 0.2s linear}.dropdown-list__item:first-child{margin-top:5px}.dropdown-list__item:last-child{margin-bottom:5px}.dropdown-list__item:not(:last-child){border-bottom:1px solid #EDF5FA}.dropdown-list__item:hover{color:#2686ED}.dropdown-list__item_selected{color:#C8CACB;cursor:default}.dropdown-list__item_selected:hover{color:#C8CACB}.dropdown-list__empty{color:#C8CACB;cursor:default}.dropdown-list__empty:hover{color:#C8CACB}.dropdown-list__loading{color:#C8CACB;cursor:default;margin-bottom:0 !important}.dropdown-list__loading:hover{color:#C8CACB}.dropdown-list__more{color:#2686ED;text-align:right;margin-bottom:0 !important}.dropdown-list__more:hover{color:#23292e}.dropdown-tags{color:#23292e;list-style-type:none;padding:0;margin:0}.dropdown-tags__wrap{border-radius:5px;border:1px solid #D0D8DC;background:white;width:100%;z-index:10;box-shadow:0px 2px 10px rgba(0,0,0,0.2)}.dropdown-tags__item{height:40px;padding:10px 15px 0 15px}.dropdown-tags__empty{color:#C8CACB}.dropdown-tags__loading:not(:first-child){border-top:1px solid #EDF5FA}.dropdown-tags__more{color:#2686ED;text-align:right;cursor:pointer;border-top:1px solid #EDF5FA}.dropdown-tags__more:hover{color:#23292e}.pseudo-input{position:relative;top:0;left:0;height:36px;padding:8px 15px 0 15px;color:#23292e;background-color:white;border:1px solid #D0D8DC;border-radius:5px;cursor:text;transition:border-color 0.2s linear,color 0.2s linear;font-size:14px}.pseudo-input_invalid{border-color:#FF9FAF;color:#FF9FAF}.pseudo-input_invalid .pseudo-input__placeholder{color:#FF9FAF}.pseudo-input_blocked{background:#F8F8F8;cursor:default}.pseudo-input_hidden{z-index:-1}.pseudo-input__placeholder{color:#C8CACB}.pseudo-input__icon-clear{width:15px;height:15px;position:absolute;right:40px;top:10px;display:flex;justify-content:center;align-items:center;cursor:pointer;color:#FF9FAF;transition:color 0.2s linear}.pseudo-input__icon-clear:hover{color:#FF3455}.pseudo-input__icon-clear:before{content:"\e926";font-family:"cp-fonts";font-size:10px}
