.user-info-popup-container{position:relative;display:flex;flex-direction:column;min-width:100px;box-shadow:0 2px 20px 0 rgba(35,41,46,0.2);border-radius:10px;background-color:#fff}.image-placeholder__wrap{display:flex;justify-content:center;align-items:center;background:#F8F8F8;border-radius:5px}.image-placeholder__icon{position:relative;height:40px;width:40px}.image-placeholder__icon:after{font-family:'cp-fonts';content:'\e922';position:absolute;width:40px;height:40px;color:#D0D8DC;font-size:40px}.user-info-popup__preloader{width:800px;height:400px;display:flex;align-items:center;justify-content:center;box-shadow:0 2px 20px 0 rgba(35,41,46,0.2);border-radius:10px;background-color:#fff;animation:smoothPopup linear 0.1s forwards}
