.cp-input-number{width:100%;height:36px;padding:0 15px;font-size:14px;border-radius:5px;border:1px solid #D0D8DC;text-align:right}.cp-input-number:disabled{background:#F8F8F8}.cp-input-number:focus{box-shadow:0px 2px 10px rgba(0,0,0,0.2)}.cp-input-number_error{border-color:#FF9FAF}.cp-input-number__wrap{position:relative;width:100%}
