.icon-context-menu__wrap{display:flex;flex-shrink:0;flex-grow:0}.icon-context-menu__item:not(:last-child){margin-right:10px}.icon-context-menu [class^="icon-"]{display:flex;justify-content:center;align-items:center;width:28px;height:28px;cursor:pointer;border-radius:50%;transition:border-color linear 0.2s, background-color linear .2s}.icon-context-menu .icon-eye,.icon-context-menu .icon-edit{color:#2686ed;background-color:#EDF5FA;border:1px solid transparent;text-decoration:none}.icon-context-menu .icon-eye:hover,.icon-context-menu .icon-edit:hover{border:1px solid #9CC8F7}.icon-context-menu .icon-eye:disabled,.icon-context-menu .icon-edit:disabled{color:#9CC8F7}.icon-context-menu .icon-eye_inactive,.icon-context-menu .icon-edit_inactive{background-color:#f1f7fc}.icon-context-menu .icon-eye_inactive:before,.icon-context-menu .icon-edit_inactive:before{color:#9CC8F7}.icon-context-menu .icon-close{font-size:10px;color:#FF3455;background-color:#FFEDF0;border:1px solid transparent}.icon-context-menu .icon-close:hover{border-color:#FF9FAF}.icon-context-menu .icon-close:disabled{color:#FF9FAF;background-color:#FFEDF0;cursor:default;pointer-events:none}
