.context-menu__wrap{z-index:999;display:flex;background-color:#fff;border-radius:5px;box-shadow:0 0 25px 0 rgba(35,41,46,0.2);padding:5px 10px;margin:auto}.context-menu__item{font-size:14px;height:33px;padding:5px;display:flex;align-items:center;white-space:nowrap}.context-menu__item:not(:last-child){border-bottom:1px solid #EDF5FA}.context-menu__item-label{line-height:18px;cursor:pointer}.context-menu_position-bottom{position:absolute;top:5px}
