.cp-tags{color:#23292e;list-style-type:none;padding:0 5px;margin:5px 0;display:flex}.cp-tags_short{flex-wrap:wrap}.cp-tags__item{position:relative;height:30px;border-radius:5px;background-color:#F8F8F8;display:flex;padding:0 10px;margin-right:5px;cursor:pointer;transition:background-color 0.2s linear,color 0.2s linear}.cp-tags__item:not(:last-child){margin-bottom:5px}.cp-tags__item:hover{background-color:#F2F2F2}.cp-tags__item_blocked{cursor:default}.cp-tags__item_blocked:hover{background-color:#F8F8F8}.cp-tags__item_selected{cursor:default;color:#D0D8DC;pointer-events:none}.cp-tags__item_selected:hover{background-color:#D0D8DC}.cp-tags__item_short{align-items:center;justify-content:center}.cp-tags__item_wide{width:100%}.cp-tags__icon-delete{position:relative;width:15px;height:15px;top:2px;display:flex;justify-content:center;align-items:center;cursor:pointer;color:#FF9FAF;transition:color 0.2s linear;margin-left:5px}.cp-tags__icon-delete:hover{color:#FF3455}.cp-tags__icon-delete:before{content:"\e926";font-family:"cp-fonts";font-size:10px}
