.input-checkbox__box{display:flex;flex-direction:column}.input-checkbox__wrap{display:flex}.input-checkbox{z-index:-1;margin-right:8px;opacity:0;position:absolute;cursor:default}.input-checkbox+.input-checkbox__label{position:relative;padding:0 0 0 30px;cursor:pointer;min-height:20px;font-size:14px;font-weight:300;display:inline-flex;align-items:center;transition:color .2s, font-weight .2s}.input-checkbox+.input-checkbox__label:before{content:'';position:absolute;top:0;left:0;width:18px;height:18px;border-radius:50%;border:1px solid #D0D8DC;transition:all .2s;background-color:#fff}.input-checkbox:disabled+.input-checkbox__label{cursor:default;pointer-events:none}.input-checkbox+.input-checkbox__label.input-checkbox__label_green{font-weight:600;font-size:14px}.input-checkbox:not(:checked)+.input-checkbox__label.input-checkbox__label_green{color:#C8CACB}.input-checkbox:disabled:not(:checked)+.input-checkbox__label:before{background:#fafafa}.input-checkbox:not(:disabled)+.input-checkbox__label:hover:before{border-color:#2686ed}.input-checkbox:not(:disabled)+.input-checkbox__label:hover:before{border-color:#23CA62}.input-checkbox:checked:not(:disabled)+.input-checkbox__label:before{border-color:#2686ed;background:#2686ed}.input-checkbox:checked:not(:disabled)+.input-checkbox__label:before{border-color:#23CA62;background:#23CA62}.input-checkbox:checked:disabled+.input-checkbox__label:before{border-color:rgba(0,0,0,0);background:rgba(38,134,237,0.4)}.input-checkbox:checked+.input-checkbox__label{font-weight:400}.input-checkbox:checked+.input-checkbox__label.input-checkbox__label_green{color:#23CA62;font-weight:600}.input-checkbox:checked+.input-checkbox__label:after{content:"\e920";position:absolute;color:#fff;font-family:"cp-fonts";font-size:12px;top:0;left:0;width:20px;height:20px;line-height:22px;text-align:center}.input-checkbox__description{position:relative;font-size:12px;margin-left:30px;margin-top:5px;color:#A6A9AA;pointer:default}
