.input-radio__box_border{padding:20px;border:1px solid #EDF5FA;border-radius:3px}.input-radio__wrap:not(:last-child){margin-bottom:10px}.input-radio__label{font-weight:300;font-size:14px}.input-radio{z-index:-1;margin-right:8px;opacity:0;position:absolute;cursor:default}.input-radio+.input-radio__label{position:relative;padding:0 0 0 30px;cursor:pointer;min-height:20px;height:20px;display:inline-flex;align-items:center}.input-radio+.input-radio__label:before{content:'';position:absolute;top:0;left:0;width:18px;height:18px;border-radius:50%;border:1px solid #D0D8DC;transition:all .2s;background-color:#fff}.input-radio:disabled+.input-radio__label{cursor:default;pointer-events:none}.input-radio:disabled+.input-radio__label:before{background:#fafafa}.input-radio:not(:disabled)+.input-radio__label:hover:before{border-color:#2686ed}.input-radio:checked+.input-radio__label{font-weight:400}.input-radio:checked+.input-radio__label:before{border-color:#2686ed}.input-radio:checked:disabled+.input-radio__label:before{border-color:rgba(38,134,237,0.4)}.input-radio+.input-radio__label:after{content:'';position:absolute;top:5px;left:5px;width:10px;height:10px;border-radius:50%;background:#2686ed;transition:all .2s;opacity:0}.input-radio:checked+.input-radio__label:after{opacity:1}.input-radio:checked:disabled+.input-radio__label:after{opacity:1;background:rgba(38,134,237,0.4)}
